'use strict';

var $ = require('jquery');
var people = {};

// Ajax using jQuery Library
// Utility Methods === Static Methods === Class Methods
//      , jQuery    
$.ajax({
  'url'       : 'https://randomuser.me/api/?results=10&gender=female',
  'dataType'  : 'json'   //  json  
})
.done(function(data) {
  people.results = data.results;
})

//     ()
exports.people = people;
